<template>


  <div class="content-container">
  
    <div class="posts-display-area">
      <div v-if="posts.length > 0" class="post-container">
       <div v-for="post in posts" :key="post.postID" class="post-card">
          <div class="post-header">
            <img :src="post.profilePicture || defaultProfilePic" alt="Profile Image" class="profile-image">
            <div class="user-info">
              <span class="user-name">{{ post.firstName }} {{ post.lastName }}</span>
              <span class="post-date">{{ formatTimestamp(post.timestamp) }}</span>
            </div>
          </div>
  
          <div class="post-body">
            <p v-if="post.content" class="post-content">{{ post.content }}</p>
            <img v-if="post.imageURL" :src="post.imageURL" alt="Post image" class="post-image">
          </div>
  
        </div>
      </div>
      <div v-else>
        <p>No posts available.</p>
      </div>
    </div>

  </div>
  
  </template>
  
  <script>
  import { DEFAULT_PROFILE_PICTURE_URL } from '../constants';
  
  export default {
    data() {
      return {
        defaultProfilePic: DEFAULT_PROFILE_PICTURE_URL,
        userProfile: JSON.parse(localStorage.getItem('user')) || {},
        followRelations: JSON.parse(localStorage.getItem('followRelations')) || [],
        posts: [],
      };
    },
    mounted() {
      this.fetchPosts();
      this.userProfile = JSON.parse(localStorage.getItem('user')) || {};
    },
    computed: {
      userImage() {
        // Return the profile picture URL from the user profile or the default if not present
        return this.userProfile.profilePicture || DEFAULT_PROFILE_PICTURE_URL;
      }
    },
    props: {
      currentUserId: Number,
      groupID: {
        type: [Number, String, null], // Accept number, string, or null
        default: null, // Default to null if not provided
      },
    },
    methods: {
      formatTimestamp(timestamp) {
        // Format the timestamp using a method of your choice. Here's an example using the JavaScript Date object:
        const date = new Date(timestamp);
        return date.toLocaleDateString("en-US"); // Format as needed
      },
  
      fetchPosts() {
        // Access the userId from the route parameters
        const userId = this.$route.params.userId;

        // Use the userId from the route to fetch the posts
        this.$axios.get(`api/profilePosts?userId=${userId}`)
          .then(response => {
            console.log("These are the fetched posts:", response.data);
            this.posts = Array.isArray(response.data) ? response.data : [];
          })
          .catch(error => {
            console.error("Error fetching posts:", error.response ? error.response.data : error.message);
          });
      }



    }
  }
  </script>
  
