<template>
  <transition name="slide">
    <div class="group-chat-window" v-if="isVisible">
      <div class="header">
        <h3>{{ groupName }} - Group Chat</h3> 
        <button @click="closeChat">x</button>
      </div>
      <div class="messages" ref="messagesContainer">
        <div v-for="(message, index) in messages" :key="index" 
          :class="{'message-sent': message.senderUserId === currentUserId, 'message-received': message.senderUserId !== currentUserId}">
          <div class="message-content-container">
            <div class="message-sender">{{ message.senderFirstName }} {{ message.senderLastName }}</div>
            <div class="message-content">{{ message.content }}</div>
          </div>
          <div class="message-timestamp">{{ new Date(message.timestamp).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }) }}</div>
        </div>
      </div>
      <div class="input-area">
        <div v-if="showEmojiPicker" class="emoji-picker">
          <button v-for="emoji in emojis" :key="emoji" @click="addEmoji(emoji)">
            {{ emoji }}
          </button>
        </div>
        <input type="text" placeholder="Type a message..." v-model="newMessage" @keyup.enter="sendMessage">
        <div class="input-area-button-container">
          <button @click="toggleEmojiPicker"> <span class="emoji">😊</span></button>
          <button @click="sendMessage">Send</button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>

export default {
  name: 'GroupChatWindow',
  props: {
    isVisible: {
      type: Boolean,
      required: true
    },
    groupId: {
      type: String,
      required: true
    },
    groupName: {
      type: String,
      required: true
    },
    messages: Array,
    currentUserId: [String, Number],
  },
  components: {
  },
  data() {
    return {
      newMessage: '',
      showEmojiPicker: false,
      emojis: ['😀', '😁', '😂', '🤣', '😃', '😄', '😅', '😆', '😉', '😊', '😋', '😎', '😍', '😘', '🥰', '😗', '😙', '😚', '🙂', '🤗','😇', '🤩', '🤔', '🤓', '😏', '😌', '😜', '🤪'],
    }
  },
  watch: {
    messages() {
      this.scrollToBottom();
    }
  },
  mounted() {
    this.scrollToBottom();
  },
  methods: {
    scrollToBottom() {
      this.$nextTick(() => {
        const messagesContainer = this.$refs.messagesContainer;
        if (messagesContainer) {
          messagesContainer.scrollTop = messagesContainer.scrollHeight;
        }
      });
    },
    sendMessage() {
      if (!this.newMessage.trim()) return;

      // Construct the message object to emit to the parent
      const messageObject = {
        content: this.newMessage,
        groupId: this.groupId
      };

      // Emit an event to the parent component with the message object
      this.$emit('sendMessage', messageObject);

      // Clear the message input
      this.newMessage = '';
    },

    toggleEmojiPicker() {
      this.showEmojiPicker = !this.showEmojiPicker;
    },
    addEmoji(emoji) {
      this.newMessage += emoji;
      this.toggleEmojiPicker(); // Optionally close the picker after an emoji is selected
    },

    closeChat() {
      this.$emit('close');
    },
  }
}
</script>

