<template>


<div class="content-container">
  <div class="post-creation-area">
    <form class="post-form" @submit.prevent="submitPost">
      <textarea v-model="newPost.content" placeholder="What's happening?"></textarea>
      <input type="file" @change="handleImageUpload">
      <div>
        <label><input type="radio" v-model="newPost.privacy" value="public"> Public</label>
        <label><input type="radio" v-model="newPost.privacy" value="private"> Private</label>
        <label><input type="radio" v-model="newPost.privacy" value="almost_private"> Almost Private</label>
      </div>
      <div v-if="newPost.privacy === 'almost_private'">
        <ul class="selected-user-list">
          <li v-for="relation in followRelations" 
              :key="relation.UserID" 
              :class="{ 'selected': isSelected(relation.UserID) }" 
              @click="toggleSelection(relation.UserID)">
            {{ relation.FirstName }} {{ relation.LastName }}
          </li>
        </ul>
      </div>
      <button type="submit">Post</button>
    </form>
  </div>

  <div class="posts-display-area">
    <div v-if="filteredPosts.length > 0">
      <div v-for="post in filteredPosts" :key="post.postID" class="post-card">
        <div class="post-header">
          <img :src="post.profilePicture || defaultProfilePic" alt="Profile Image" class="profile-image">
          <div class="user-info">
            <span class="user-name">{{ post.firstName }} {{ post.lastName }}</span>
            <span class="post-date">{{ formatTimestamp(post.timestamp) }}</span>
          </div>
        </div>

        <div class="post-body">
          <p v-if="post.content" class="post-content">{{ post.content }}</p>
          <img v-if="post.imageURL" :src="post.imageURL" alt="Post image" class="post-image">
        </div>

        <div class="post-actions">
          <button class="post-action-button" @click="openModal(post)">Comment</button>
          <!-- Other buttons if needed -->
        </div>
      </div>
    </div>
    <div v-else>
      <p>No posts available.</p>
    </div>
  </div>

    <!-- Modal for post details and commenting -->
    <div v-if="activePost" class="post-modal">
      <div class="post-modal-content">
        <div class="modal-header">
          <img :src="activePost.profilePicture || defaultProfilePic" alt="Profile Image" class="profile-image">
          <div class="user-info">
            <span class="user-name">{{ activePost.firstName }} {{ activePost.lastName }}</span>
            <span class="post-date">{{ formatTimestamp(activePost.timestamp) }}</span>
          </div>
        </div>
        <div class="modal-body">
          <p>{{ activePost.content }}</p>
          <img v-if="activePost.imageURL" :src="activePost.imageURL" alt="Post image" class="post-image">
        </div>
        <div class="modal-footer">
          <form @submit.prevent="submitComment" enctype="multipart/form-data" class="comment-form">
            <div class="comment-profile">
              <img :src="userImage || defaultProfilePic" alt="Profile" class="comment-profile-image">
            </div>
            <textarea 
              v-model="newComment" 
              placeholder="Write a comment..."
              rows="2"
              class="comment-textarea">
            </textarea>
            <input class="comment-file-upload" type="file" @change="handleCommentImageUpload" />
            <button type="submit" class="comment-submit">Comment</button>
          </form>
          <div v-for="comment in comments" :key="comment.commentID" class="comment-container">
            <div class="comment-profile">
              <img :src="comment.profilePicture || defaultProfilePic" alt="Profile" class="comment-profile-image">
            </div>
