              <div class="comment-header">
                <span class="comment-author">{{ comment.firstName }} {{ comment.lastName }}</span>
                <span class="comment-timestamp">{{ formatTimestamp(comment.timestamp) }}</span>
              </div>
              <div class="comment-content">
                {{ comment.content }}
                <div class="comment-media-container" v-if="comment.commentMedia">
                  <img :src="comment.commentMedia" alt="Comment media" />
                </div>
              </div>
              <div class="comment-actions">
                <!-- Insert any action buttons here -->
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="post-modal-backdrop" @click="closeModal"></div>
    </div>

</div>

</template>

<script>
import axios from 'axios';
import { DEFAULT_PROFILE_PICTURE_URL } from '../constants';

export default {
  data() {
    return {
      defaultProfilePic: DEFAULT_PROFILE_PICTURE_URL,
      userProfile: JSON.parse(localStorage.getItem('user')) || {},
      newPost: {
        content: '',
        privacy: 'public',
        image: null, // Initialize image property as null
        selectedUserIds: []
      },
      followRelations: JSON.parse(localStorage.getItem('followRelations')) || [],
      posts: [],
      activePost: null,
      newComment: '',
      comments: [],
    };
  },
  mounted() {
    this.fetchPosts();
    this.userProfile = JSON.parse(localStorage.getItem('user')) || {};
  },
  computed: {
    filteredPosts() {
      return this.posts ? this.posts.filter(post => this.isAllowedToView(post)) : [];
    },
    userImage() {
      // Return the profile picture URL from the user profile or the default if not present
      return this.userProfile.profilePicture || DEFAULT_PROFILE_PICTURE_URL;
    }
  },
  props: {
    currentUserId: Number,
    groupID: {
      type: [Number, String, null], // Accept number, string, or null
      default: null, // Default to null if not provided
    },
  },
  methods: {
    formatTimestamp(timestamp) {
      // Format the timestamp using a method of your choice. Here's an example using the JavaScript Date object:
      const date = new Date(timestamp);
      return date.toLocaleDateString("en-US"); // Format as needed
    },
    handleImageUpload(event) {
      this.newPost.image = event.target.files[0];
    },

    toggleSelection(userId) {
      const index = this.newPost.selectedUserIds.indexOf(userId);
      if (index > -1) {
        this.newPost.selectedUserIds.splice(index, 1); // remove user ID
      } else {
        this.newPost.selectedUserIds.push(userId); // add user ID
      }
    },

    isSelected(userId) {
      return this.newPost.selectedUserIds.includes(userId);
    },

    submitPost() {
      const formData = new FormData();
      formData.append('content', this.newPost.content);
      formData.append('privacy', this.newPost.privacy);
      formData.append('selectedUserIds', JSON.stringify(this.newPost.selectedUserIds));
      if (this.newPost.image) {
        formData.append('image', this.newPost.image);
      }
      // Include groupID in the request if it's not null
      if (this.groupID) {
        formData.append('groupID', this.groupID);
      }
      this.$axios.post('/api/createPost', formData) // Adjust the endpoint if needed
        .then(response => {
          this.posts = [response.data, ...this.posts];
          console.log("This is the created Post:", response)
          this.newPost = { content: '', privacy: 'public', image: null }; // Reset the form
        })
        .catch(error => {
          console.error('Error creating post:', error);
        });
    },

    fetchPosts() {
      console.log('Fetching posts for group:', this.groupID)
      let apiUrl = '/api/posts';
      if (this.groupID) {
        apiUrl += `?groupID=${this.groupID}`;
      }
      this.$axios.get(apiUrl)
        .then(response => {
          console.log("These are the fetched posts:", response.data);
          this.posts = response.data;
        })
        .catch(error => {
          console.error('Error fetching posts:', error);
        });
    },


    fetchComments(postID) {
      this.$axios.get(`/api/getComments`, { params: { postID: postID } })
      .then(response => {
        console.log("Comments fetched:", response.data);
        this.comments = response.data;
      })
      .catch(error => {
        console.error('Error fetching comments:', error);
      });
    },
    
    isAllowedToView(post) {
      const currentUserID = this.currentUserId;

      if (post.privacySetting === 'private') {
        // Check if there is a follow relation between the current user and the post's user
        return this.followRelations.some(relation => relation.UserID === post.UserID);
      }

      if (post.privacySetting === 'almost_private') {
        // Parse the allowed viewers from the string to an array
        const allowedViewers = JSON.parse(post.allowedViewers);
        return allowedViewers.includes(currentUserID);
      }

      return true; // Public posts are viewable by everyone
    },

    openModal(post) {
      this.activePost = post;
      this.fetchComments(post.postID);
    },

    closeModal() {
      this.activePost = null;
      this.newComment = '';
    },

    handleCommentImageUpload(event) {
      this.commentImage = event.target.files[0];
    },

    submitComment() {
      const formData = new FormData();
      formData.append('postID', this.activePost.postID);
      formData.append('userID', this.currentUserId);
      formData.append('content', this.newComment);
      if (this.commentImage) {
          formData.append('image', this.commentImage);
      }

      axios.post('/api/createComment', formData)
      .then(response => {
        console.log("Comment created:", response.data);
        this.comments = [response.data, ...this.comments];
        this.newComment = '';
        this.commentImage = null;
      })
      .catch(error => {
          console.error('Error creating comment:', error);
      });
    },


  }
}
</script>

