<template>
  <div class="chat-window" v-if="currentRoomId">
    <div class="chat-header">
      <span>{{ chatPartnerName }}</span>
      <span class="close" @click="closeChat">&times;</span>
    </div>
    <ul class="chat-messages" ref="messagesContainer">
      <li v-for="message in messagesForCurrentRoom" :key="message.messageId"
      :class="{'message-sent': isCurrentUserSender(message), 'message-received': !isCurrentUserSender(message)}">
        <div class="message-content">{{ message.content }}</div>
        <div class="message-timestamp">{{ new Date(message.timestamp).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}}</div>
      </li>
    </ul>
    <div class="chat-input">
      <div v-if="showEmojiPicker" class="emoji-picker">
        <button v-for="emoji in emojis" :key="emoji" @click="addEmoji(emoji)">
          {{ emoji }}
        </button>
      </div>
      <input type="text" v-model="newMessage" @keyup.enter="sendMessage" placeholder="Type a message...">
      <div class="input-area-button-container">
        <button @click="toggleEmojiPicker"> <span class="emoji">😊</span></button>
        <button @click="sendMessage">Send</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      newMessage: "",
      showEmojiPicker: false,
      emojis: ['😀', '😁', '😂', '🤣', '😃', '😄', '😅', '😆', '😉', '😊', '😋', '😎', '😍', '😘', '🥰', '😗', '😙', '😚', '🙂', '🤗','😇', '🤩', '🤔', '🤓', '😏', '😌', '😜', '🤪'],
    };
  },
  props: {
    currentRoomId: String,
    messages: Array,
    sendMessageFunction: Function,
    currentUserId: {
      type: [String, Number],
      required: true
    }
  },
  computed: {
    messagesForCurrentRoom() {
      return this.messages.filter(m => m.roomId === this.currentRoomId);
    },

    isCurrentUserSender() {
      return message => message.senderUserId === this.currentUserId;
    },

    chatPartnerName() {
      if (!this.messagesForCurrentRoom.length) return 'User Name';

      const firstMessage = this.messagesForCurrentRoom[0];

      if (firstMessage.senderUserId === this.currentUserId) {
        return `${firstMessage.receiverFirstName} ${firstMessage.receiverLastName}`;
      } else {
        return `${firstMessage.senderFirstName} ${firstMessage.senderLastName}`;
      }
    },

  },
  watch: {
    messages() {
      this.scrollToBottom();
    }
  },
  
  mounted() {
    this.scrollToBottom();
  },
  methods: {
    scrollToBottom() {
      this.$nextTick(() => {
        const messagesContainer = this.$refs.messagesContainer;
        if (messagesContainer) {
          messagesContainer.scrollTop = messagesContainer.scrollHeight;
        }
      });
    },

    sendMessage() {
      if (!this.newMessage.trim()) return;
      if (this.newMessage !== "") {
        this.sendMessageFunction(this.newMessage, this.currentRoomId);
        this.newMessage = "";
      }
    },

    checkMessageSender(message) {
      console.log(`Message ID: ${message.messageId}, Sender ID: ${message.senderUserId}, Current User ID: ${this.currentUserId}`);
      return message.senderUserID === this.currentUserId;
    },

    closeChat() {
      this.$emit('closeChat', this.currentRoomId);
    },

    toggleEmojiPicker() {
      this.showEmojiPicker = !this.showEmojiPicker;
    },
    addEmoji(emoji) {
      this.newMessage += emoji;
      this.toggleEmojiPicker(); // Optionally close the picker after an emoji is selected
    },

  },
}
</script>


