<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
export default {
  name: 'App',
  created() {
    this.checkCookie();
  },
  methods: {
    checkCookie() {
      const sessionIdCookie = document.cookie.split(';').find(row => row.trim().startsWith('session_id='));
      if (!sessionIdCookie) {
        localStorage.clear();
        this.$store.dispatch('logOut');
        this.$router.push('/welcome');
      } 
    }
  }
}
</script>

<style>
#app {
  font-family: Segoe UI Historic, Segoe UI, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>
