<template>
  <header>
    <div class="header-element-wrapper">
      <h1>Welcome to {{ groupName }}</h1>
    </div>
    <div class="header-element-wrapper">
      <nav class="navbar">
        <router-link to="/" class="nav-item">HOME</router-link>
        <router-link to="/groups" class="nav-item">GROUPS</router-link>
        <router-link to="/users" class="nav-item">USERS</router-link> 
        <router-link :to="`/profile/${currentUserId}`" class="nav-item">PROFILE</router-link>
      </nav>
    </div>
    <div class="header-element-wrapper">
      <button @click="handleLogout">Log Out</button>
    </div>
  </header>

  <div class="main-container">
    <div class="content-wrapper">
      <div class="group-detail-content">
        <ContentContainer 
        :currentUserId="currentUserId" 
        :groupID="groupId"
        />
      </div>
      <aside class="user-list">
        <h2>Group Members</h2>
        <ul>
          <li v-for="member in groupMembers" :key="member.UserID">
            <img :src="member.profilePicture || defaultProfilePic" alt="Profile Image" class="profile-image">
            {{ member.firstName + ' ' + member.lastName }}
          </li>
        </ul>
        <GroupChatWindow
          :isVisible="showGroupChat"
          :groupId="groupId"
          :groupName="groupName"
          :messages="messages.filter(m => m.roomId === currentRoomId)"
          :currentUserId="currentUserId"
          @close="showGroupChat = false"
          @sendMessage="sendChatMessage"
        />
        <button v-if="isGroupMember" @click="connectToGroupChat(groupId)" class="join-groupchat-btn">Join Group Chat</button>
      </aside>
      <aside class="left-sidebar">
        <div class="action-buttons-left-sidebar">
          <button @click="toggleNotificationCenter()">Notification Center</button>
          <div v-if="isGroupMember">
            <button @click="isCreatEventVisible = !isCreatEventVisible">Create New Event</button>
            <button @click="toggleInviteList">Invite Users</button>
          </div>
        </div>
          
        <div v-if="showInviteList" class="invite-users-list">
          <ul>
            <li v-for="user in filteredUsers" :key="user.userID">
              <input type="checkbox" :id="`invite-${user.userID}`" :value="user.userID" v-model="selectedUserIds">
              <label :for="`invite-${user.userID}`">{{ user.firstName + ' ' + user.lastName }}</label>
            </li>
          </ul>
          <button @click="submitInvitations" class="invite-button">Send Invites</button>
        </div>
        <div class="event-list">
          <div v-if="events && events.length > 0">
            <h3>Events</h3>
            <br>
            <div v-for="event in events" :key="event.EventID" @click="toggleEvent(event.EventID)" :class="{ 'event': true, 'expanded': expandedEventId === event.EventID }">
            <div>
              <h3>{{ event.Title }}</h3>
              <p>{{ new Date(event.EventDateTime).toLocaleString() }}</p>
            </div>
            <div class="event-details" v-if="expandedEventId === event.EventID">
              <p>Created by: {{ event.FirstName }} {{ event.LastName }}</p>
              <p>{{ event.Description }}</p>
              <div v-if="event.Going.length > 0">
                <p>Going:</p>
                <ul>
                  <li v-for="user in event.Going" :key="user">{{ user }}</li>
                </ul>
              </div>
              <div v-if="event.NotGoing.length > 0">
                <p>Not Going:</p>
                <ul>
                  <li v-for="user in event.NotGoing" :key="user">{{ user }}</li>
                </ul>
              </div>
            </div>
          </div>
          </div>
        </div>
      </aside>


      <aside class="event-creation-form" :class="{ 'is-visible': isCreatEventVisible }">
        <div class="event-creation-sidebar-header">
          <button @click="isCreatEventVisible = false">
            <span class="sidebar-back">
              <svg version="1.0" xmlns="http://www.w3.org/2000/svg"
                width="512.000000pt" height="512.000000pt" viewBox="0 0 512.000000 512.000000"
                preserveAspectRatio="xMidYMid meet">

                <g transform="translate(0.000000,512.000000) scale(0.100000,-0.100000)"
                fill="#000000" stroke="none">
                <path d="M2058 4727 c-31 -13 -74 -38 -95 -55 -77 -62 -1882 -1878 -1907
                -1920 -38 -61 -60 -154 -52 -225 14 -132 -40 -73 1014 -1129 795 -796 975
                -971 1020 -994 78 -39 202 -46 285 -14 89 34 153 90 191 169 28 60 31 75 31
                161 0 165 16 144 -562 729 -274 278 -534 536 -579 575 -45 40 -118 91 -167
                116 l-86 45 1837 5 1837 5 57 23 c81 33 160 108 200 190 30 60 33 75 33 152
                -1 70 -5 95 -27 142 -35 76 -99 143 -173 181 l-60 32 -1855 5 -1855 5 95 50
                95 49 576 576 c665 664 634 624 634 795 0 89 -3 106 -28 156 -15 31 -50 78
                -77 103 -72 68 -126 89 -235 93 -77 3 -98 0 -147 -20z"/>
                </g>
              </svg>
            </span>
          </button>
          <h2>Create New Event</h2>
        </div>
        <div class="create-event-body">
          <form @submit.prevent="submitEvent">
            <div class="form-group">
              <label for="eventName">Event Title:</label>
              <input type="text" id="eventName" v-model="eventName" required>
            </div>

            <div class="form-group">
              <label for="eventDescription">Description:</label>
              <textarea id="eventDescription" v-model="eventDescription" rows="5" required></textarea>
            </div>

            <div class="form-group">
              <label for="eventDateTime">Day/Time:</label>
              <input type="datetime-local" id="eventDateTime" v-model="eventDateTime" required>
            </div>

            <label>Invite Group Members:</label>
            <ul class="user-invitation-list">
              <li v-for="member in groupMembers" :key="member.userID">
                <input
                  type="checkbox"
                  :id="`invite-${member.userID}`"
                  :value="member.userID"
                  v-model="selectedUserIds"
                >
                <label :for="`invite-${member.userID}`">
                  {{ member.firstName + ' ' + member.lastName }}
                </label>
              </li>
            </ul>

            <button type="submit">Create Event</button>
          </form>
        </div>
      </aside>
      <aside class="notification-center" :class="{ 'is-visible': isNotificationCenterVisible }">
        <div class="notification-center-header">
          <button @click="isNotificationCenterVisible = false; notifications = [];">
            <span class="sidebar-back">
              <svg version="1.0" xmlns="http://www.w3.org/2000/svg"
                width="512.000000pt" height="512.000000pt" viewBox="0 0 512.000000 512.000000"
                preserveAspectRatio="xMidYMid meet">

                <g transform="translate(0.000000,512.000000) scale(0.100000,-0.100000)"
                fill="#000000" stroke="none">
                <path d="M2058 4727 c-31 -13 -74 -38 -95 -55 -77 -62 -1882 -1878 -1907
                -1920 -38 -61 -60 -154 -52 -225 14 -132 -40 -73 1014 -1129 795 -796 975
                -971 1020 -994 78 -39 202 -46 285 -14 89 34 153 90 191 169 28 60 31 75 31
                161 0 165 16 144 -562 729 -274 278 -534 536 -579 575 -45 40 -118 91 -167
                116 l-86 45 1837 5 1837 5 57 23 c81 33 160 108 200 190 30 60 33 75 33 152
                -1 70 -5 95 -27 142 -35 76 -99 143 -173 181 l-60 32 -1855 5 -1855 5 95 50
                95 49 576 576 c665 664 634 624 634 795 0 89 -3 106 -28 156 -15 31 -50 78
                -77 103 -72 68 -126 89 -235 93 -77 3 -98 0 -147 -20z"/>
                </g>
              </svg>
            </span>
          </button>

          <h2>Notifications</h2>
        </div>
        <div class="notification-list">
          <div v-for="notification in notifications" :key="notification.id" class="notification">
            <div v-if="notification.type === 'eventInvite'">
              <div v-if="notificationMessage" class="notification-message">{{ notificationMessage }}</div>
              <p class="event-invite-message">
                You have been invited to <strong>{{ notification.groupName }}</strong> event <strong>"{{ notification.title }}"</strong>
                by <strong>{{ notification.creatorFirstName }} {{ notification.creatorLastName }}</strong>
              </p>
              <div class="response-buttons">
                <button class="going" @click="handleResponse(notification, 'Going', 'event')">Going</button>
                <button class="notGoing" @click="handleResponse(notification, 'Not Going', 'event')">Not Going</button>
              </div>
            </div>

            <div v-else-if="notification.type === 'groupInvite'">
              <!-- New groupInvite notification UI -->
              <p class="group-invite-message">
                You have been invited to join the group <strong>"{{ notification.groupName }}"</strong>
                by <strong>{{ notification.creatorName }}</strong>. <!-- Consider fetching user's name if possible -->
              </p>
              <div class="response-buttons">
                <button class="accept" @click="handleResponse(notification, true, 'group')">Accept</button>
                <button class="decline" @click="handleResponse(notification, false, 'group')">Decline</button>
              </div>
            </div>

            <div v-else-if="notification.type === 'followRequest'">
              <p class="follow-request-message">
                  <strong>{{ notification.firstName }} {{ notification.lastName }}</strong> wants to follow you.
              </p>
              <div class="response-buttons">
                <button class="accept" @click="handleResponse(notification, true, 'follow')">Accept</button>
                <button class="decline" @click="handleResponse(notification, false, 'follow')">Decline</button>
              </div>
            </div>

            <div v-if="notification.type === 'groupJoinRequest'">
              <p class="group-join-request-message">
                <strong>{{ notification.FirstName }} {{ notification.LastName }}</strong> wants to join your group <strong>"{{ notification.groupName }}"</strong>.
              </p>
              <div class="response-buttons">
                <button class="accept" @click="handleResponse(notification, true, 'groupJoinRequest')">Accept</button>
                <button class="decline" @click="handleResponse(notification, false, 'groupJoinRequest')">Decline</button>
              </div>
            </div>
          </div>
        </div>
      </aside>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';
import { DEFAULT_PROFILE_PICTURE_URL } from '@/constants';
import ContentContainer from '@/components/ContentContainer.vue';
import GroupChatWindow from '@/components/GroupChatWindow.vue';

export default {
  name: 'GroupDetail',
  components: {
    ContentContainer,
    GroupChatWindow,
  },
  data() {
    return {
      defaultProfilePic: DEFAULT_PROFILE_PICTURE_URL,
      group: null,
      groupId: this.$route.params.groupId,
      ws: null, // WebSocket instance
      serverUrl: "ws://localhost:8091/ws",
      messages: [],
      unreadMessagesCount: {},
      newMessage: "",
      currentRoomId: null,
      openChats: {},
      followRelations: [],
      isCreatEventVisible: false,
      availableUsers: [],
      events: [],
      expandedEventId: null,
      eventName: '',
      eventDescription: '',
      eventDateTime: '',
      selectedUserIds: [],
      groupName: '',
      groupDescription: '',
      groupMembers: [],
      showGroupChat: false,
      invitedUserIds: [],
      filteredUsers: [],
      showInviteList: false,
      notifications: [],
      notificationMessage: '',
      isNotificationCenterVisible: false,
    };
  },
  watch: {
    // Watch for changes in `groupId` and log the change
    currentRoomId(newValue, oldValue) {
      console.log(`currenRoomId changed from ${oldValue} to ${newValue}`);
    },
    'group.name'(newValue, oldValue) {
      console.log(`Group name changed from ${oldValue} to ${newValue}`);
    },
    messages: {
      handler(newValue) {
        console.log('Messages array changed.', newValue);
      },
      deep: true
    },
    showGroupChat(newValue) {
      console.log(`showGroupChat changed to ${newValue}`);
    },
    events: {
      handler(newValue, oldValue) {
        console.log(`Events changed`, newValue, oldValue);
      },
      deep: true
    }
  },
  mounted() {
    this.loadUserRelations();
    this.connectToWebsocket();
    this.fetchGroupDetail();
    this.fetchGroupMembers();
    this.fetchEvents();
    const joinGroupChatData = JSON.parse(localStorage.getItem('joinGroupChatData'));
    if (joinGroupChatData) {
      // Handle the join group chat response data
      console.log("Group chatroom ID:", joinGroupChatData);
      // Optionally, clear the storage if it's a one-time read
      localStorage.removeItem('joinGroupChatData');
    }
  },
  computed: {
    ...mapState(['isLoggedIn', 'currentUser']),
    currentUserId() {
      const user = JSON.parse(localStorage.getItem('user'));
      return user ? user.userID : null;
    },

    filteredOpenChats() {
      if (!Array.isArray(this.followRelations)) {
        return []; // Return an empty array if followRelations is not an array
      }
      return this.followRelations.filter(relation => this.openChats[relation.RoomID]);
    },
    isGroupMember() {
      return this.groupMembers.includes(this.currentUserId);
    },
  },
  methods: {
    ...mapActions(['logOut']),
    handleLogout() {
      this.$axios.post('/api/logout')
      .then(() => {
        this.logOut(); 
        this.$cookies.remove('session_id');
        localStorage.clear();
        this.$router.push('/welcome');
      })
      .catch(error => {
        console.error('Logout failed:', error);
      });
    },

    loadUserRelations() {
      const relations = localStorage.getItem('userRelations');
      if (relations) {
        try {
          const parsedRelations = JSON.parse(relations);
          this.followRelations = parsedRelations.map(relation => {
            // Initialize unreadMessagesCount for the room if it doesn't exist
            if (this.unreadMessagesCount[relation.RoomID] === undefined) {
              this.unreadMessagesCount[relation.RoomID] = relation.UnreadCount || 0;
            }
            return {
              ...relation,
              UnreadCount: this.unreadMessagesCount[relation.RoomID]
            };
          });
        } catch (error) {
          console.error('Error parsing followRelations from localStorage:', error);
        }
      } else {
        console.log('No followRelations found in localStorage');
        this.followRelations = [];
      }
    },

    connectToWebsocket() {
      // Check if an existing WebSocket connection is open
      if (this.ws && this.ws.readyState === WebSocket.OPEN) {
        console.log("WebSocket already open.");
        return; // Exit if connection is already established
      }

      console.log("Attempting to connect to WebSocket");
      this.ws = new WebSocket(this.serverUrl);

      // Attach event listeners
      this.ws.addEventListener('open', this.onWebsocketOpen);
      this.ws.addEventListener('message', (event) => { this.handleNewMessage(event) });
      this.ws.addEventListener('error', this.onWebsocketError);
      this.ws.addEventListener('close', this.onWebsocketClose);
    },

    onWebsocketOpen() {
      console.log("Connected to WS!");
      // Handle WebSocket open event
    },

    onWebsocketError(error) {
      // Handle WebSocket errors
      console.error("WebSocket Error: ", error);
    },

    onWebsocketClose() {
      // Handle WebSocket close event
      console.log("WebSocket connection closed");
    },

    handleNewMessage(event) {
      console.log("Received WebSocket message:", event.data);

      // Split the received data by newline and filter out any empty strings
      const messages = event.data.split('\n').filter(msg => msg.trim());

      let data;

      // Process each message individually
      messages.forEach((message) => {
        try {
          data = JSON.parse(message);
          // Now handle the parsed data object as before
          switch (data.type) {
            case 'joinGroupChatResponse':
              this.handleJoinGroupChatResponse(data.payload);
            break;
            case 'chatMessage':
              if (data.payload.roomId === this.currentRoomId) {
                this.messages.push(data.payload);
              } else {
                // Increment unread message count if ChatWindow is not open
                if (!this.openChats[data.payload.roomId]) {
                  this.updateUnreadCount(data.payload.roomId);
                }
              }
            break;
            case 'fetchMessagesResponse':
              console.log("Fetched messages:", data.payload);
              if (data.payload.roomId === this.currentRoomId) {
                // Check if messages are present in the payload
                if (data.payload.messages && data.payload.messages.length > 0) {
                  const fetchedMessages = data.payload.messages.reverse();
                  this.messages = [...fetchedMessages, ...this.messages];
                }
              }
            break;
            case 'eventInviteResponse':
              console.log("Event invite response payload:", data.payload);
              if (Array.isArray(data.payload)) {
                data.payload.forEach(invite => {
                  // Check if a notification with the same responseId already exists in the array
                  const existingNotificationIndex = this.notifications.findIndex(notification => {
                    return (
                      notification.type === 'eventInvite' &&
                      notification.responseId === invite.responseId
                    );
                  });

                  if (existingNotificationIndex === -1) {
                    // If no duplicate is found, add the new notification
                    this.notifications.push({
                      type: 'eventInvite',
                      ...invite,
                    });
                  } else {
                    // If a duplicate is found, you can update the existing notification or skip it
                    // For example, updating the existing notification with new data
                    this.notifications[existingNotificationIndex] = {
                      ...this.notifications[existingNotificationIndex],
                      ...invite,
                    };
                  }
                });
              } else {
                if (data.payload.message) {
                  this.notificationMessage = (data.payload.message);
                }
              }
            break;
            case 'groupInviteResponse':
              console.log("Group invite response payload:", data.payload);
              if (Array.isArray(data.payload)) {
                data.payload.forEach(invite => {
                  // Construct a notification object for the group invite
                  const groupInviteNotification = {
                    type: 'groupInvite', // This is the type for group invite notifications
                    groupId: invite.GroupID,
                    groupName: invite.Name,
                    groupDescription: invite.Description,
                    creatorUserId: invite.CreatorUserID,
                    creatorName: invite.CreatorName
                  };
                  // Check if a notification for the same group already exists
                  const existingNotificationIndex = this.notifications.findIndex(notification => {
                    return notification.type === 'groupInvite' && notification.groupId === invite.GroupID;
                  });

                  if (existingNotificationIndex === -1) {
                    // If no duplicate is found, add the new notification
                    this.notifications.push(groupInviteNotification);
                  } else {
                    // If a duplicate is found, you can decide to update the existing notification or simply skip
                    // For example, updating the existing notification with new data
                    this.notifications[existingNotificationIndex] = {
                      ...this.notifications[existingNotificationIndex],
                      ...groupInviteNotification,
                    };
                  }
                });
              } else {
                // If the payload is not an array and contains a message, handle it (e.g., show a message to the user)
                if (data.payload.message) {
                  this.notificationMessage = data.payload.message;
                }
              }
            break;
            case 'followRequestResponse':
              console.log("Follow request payload:", data.payload);
              if (Array.isArray(data.payload)) {
                data.payload.forEach(followRequest => {
                  // Construct a notification object for the follow request
                  const followRequestNotification = {
                    type: 'followRequest', // This is the type for follow request notifications
                    followerUserId: followRequest.followerUserId,
                    firstName: followRequest.firstName,
                    lastName: followRequest.lastName,
                    // Add any other fields you need for the notification
                  };

                  // Check if a notification for the same follow request already exists
                  // You might use a unique identifier, like followerUserId, to check for duplicates
                  const existingNotificationIndex = this.notifications.findIndex(notification => {
                    return notification.type === 'followRequest' && notification.followerUserId === followRequest.followerUserId;
                  });

                  if (existingNotificationIndex === -1) {
                    // If no duplicate is found, add the new notification
                    this.notifications.push(followRequestNotification);
                  } else {
                    // If a duplicate is found, you can decide to update the existing notification or simply skip
                    // For example, updating the existing notification with new data
                    this.notifications[existingNotificationIndex] = {
                      ...this.notifications[existingNotificationIndex],
                      ...followRequestNotification,
                    };
                  }
                });
              }

            break;
            case 'groupJoinRequestResponse':
              console.log("Group join request payload:", data.payload);
              if (Array.isArray(data.payload)) {
                data.payload.forEach(request => {
                  // Construct a notification object for the group join request
                  const groupJoinRequestNotification = {
                    type: 'groupJoinRequest',
                    requestId: request.requestId,
                    groupId: request.groupId,
                    groupName: request.groupName,
                    requesterUserId: request.userId,
                    LastName: request.lastName,
                    FirstName: request.firstName,
                  };

                  // Add the new notification
                  this.notifications.push(groupJoinRequestNotification);
                });
              }
            break;
            }
        } catch (error) {
          console.error("Error parsing individual message:", error);
        }
      });
      if (data.userRelations) {
        // Handle and save userRelations
        console.log("User relations received:", data.userRelations);
        const userRelationsArray = Object.values(data.userRelations);
        localStorage.setItem('userRelations', JSON.stringify(userRelationsArray));
      }
      if (data.followingMap) {
        // Handle and save followingMap
        console.log("Following map received:", data.followingMap);
        localStorage.setItem('followingMap', JSON.stringify(data.followingMap));
      }
      if (data.followersMap) {
        // Handle and save followersMap
        console.log("Followers map received:", data.followersMap);
        localStorage.setItem('followersMap', JSON.stringify(data.followersMap));
      }
      if (data.pendingRequests) {
        console.log("Requests map received:", data.pendingRequests);
        localStorage.setItem('pendingRequests', JSON.stringify(data.pendingRequests));
      }
      if (data.userGroups) {
        console.log("User groups received:", data.userGroups);
        localStorage.setItem('userGroups', JSON.stringify(data.userGroups));
      }
      if (data.groupJoinRequests) {
        console.log("Group join requests received:", data.groupJoinRequests);
        localStorage.setItem('groupJoinRequests', JSON.stringify(data.groupJoinRequests));
      }
    },


    fetchGroupDetail() {
      const groupId = this.$route.params.groupId;
      this.$axios.post(`/api/group/details`, { groupId: groupId }, { withCredentials: true })
        .then(response => {
          console.log(response)
            this.group = response.data;
            const groupData = response.data
            this.groupName = groupData.name;
            this.groupDescription = groupData.description;
        })
        .catch(error => {
            console.error('Failed to fetch group details:', error);
        });
    },

    sendChatMessage(messageObject) {
      const currentUser = JSON.parse(localStorage.getItem('user'));
      const senderUserId = currentUser.userID;
      const timestamp = new Date();

      // Ensure groupId is an integer if it exists
      const groupId = messageObject.groupId ? parseInt(messageObject.groupId, 10) : undefined;
      // Add a check to ensure groupId is a number before including it
      const payloadGroupId = !isNaN(groupId) ? groupId : undefined;

      // Construct the message payload
      let messagePayload = {
        type: "chatMessage",
        payload: {
          senderUserId: senderUserId,
          content: messageObject.content,
          timestamp: timestamp.toISOString(),
          groupId: payloadGroupId,
          roomId: this.currentRoomId ? this.currentRoomId : undefined
        }
      };

      console.log("Sending chat message:", JSON.stringify(messagePayload));
      this.ws.send(JSON.stringify(messagePayload));
    },

    toggleInviteList() {
      this.showInviteList = !this.showInviteList;
      if (this.showInviteList) {
        this.prepareInviteList();
      }
    },
    prepareInviteList() {
      Promise.all([this.fetchUsers(), this.fetchGroupMembers(), this.fetchInvitedUsers()])
        .then(() => {
          this.filterOutGroupMembersAndInvitedUsers();
        })
        .catch(error => console.error("Error preparing invite list:", error));
    },


    fetchMessages(roomId) {
      const groupIdInt = this.groupId ? parseInt(this.groupId, 10) : null;
      let fetchPayload = {
        type: "fetchMessages", // Updated type to reflect the new functionality
        payload: {
          roomId: roomId,
          ...(Number.isInteger(groupIdInt) && { groupId: groupIdInt })
        }
      };

      console.log("Fetching messages for room:", roomId);
      console.log("Fetching messages with payload:", fetchPayload);
      this.ws.send(JSON.stringify(fetchPayload));
    },

    handleCloseChat(roomId) {
      // Set the chat window state to closed for the given room ID
      if (this.openChats[roomId] !== undefined) {
        this.openChats[roomId] = false;
      }
    },

    fetchUsers() {
      return this.$axios.get('/api/users')
        .then(response => {
          // Assuming response.data is an array of user objects
          this.availableUsers = response.data;
        })
        .catch(error => {
          console.error('Error fetching users:', error);
          throw error; // Rethrow to be caught in prepareInviteList
        });
    },

    fetchInvitedUsers() {
      const payload = { groupId: parseInt(this.groupId, 10) };
      return this.$axios.post('/api/invitedUsers', payload) // Adjust the endpoint as necessary
        .then(response => {
          // Assuming response.data is an array of user IDs
          this.invitedUserIds = response.data.map(invite => invite.userID);
        })
        .catch(error => {
          console.error('Error fetching invited users:', error);
          throw error; // Rethrow to be caught in prepareInviteList
        });
    },


    filterOutGroupMembersAndInvitedUsers() {
      const excludedUserIDs = new Set([...this.groupMembers.map(member => member.userID), ...this.invitedUserIds]);
      this.filteredUsers = this.availableUsers.filter(user => !excludedUserIDs.has(user.userID));
    },


    submitInvitations() {
      const payload = {
        groupId: parseInt(this.groupId, 10), // Assuming this.groupId holds the current group's ID
        invitedUserIds: this.selectedUserIds // Assuming this.selectedUserIds holds the array of user IDs to invite
      };

      console.log(typeof(payload.invitedUserIds), payload.invitedUserIds);
      console.log(typeof(payload.groupId), payload.groupId);

      this.$axios.post('/api/inviteUsers', payload)
        .then(response => {
          console.log('Invitations sent successfully:', response.data);
          alert('Invitations sent successfully.');
          // Handle successful invitation sending here (e.g., clear selection)
          this.selectedUserIds = [];
          location.reload();
        })
        .catch(error => {
          console.error('Error sending invitations:', error);
          // Handle errors here
        });
    },

    submitGroup() {
      // Log the groupData before sending the request
      console.log('Submitting group data:', this.groupName, this.groupDescription, this.selectedUserIds);

      const groupData = {
        group: {
          name: this.groupName,
          description: this.groupDescription
        },
        invitedUserIds: this.selectedUserIds,
      };

      this.$axios.post('/api/createGroup', groupData)
      .then(response => {
        // Handle successful group creation
        console.log('Group created:', response.data);
        
        // Add the newly created group to the groups array to display it instantly
        this.groups.push(response.data);

        // Clear the form
        this.groupName = '';
        this.groupDescription = '';
        this.selectedUserIds = [];

        // Optionally, you can redirect to the groups page
        // this.$router.push('/groups');
      })
      .catch(error => {
        console.error('Error creating group:', error);
      });
    },

    fetchGroupMembers() {
      this.$axios.get(`/api/groupMembers?groupID=${this.groupId}`)
        .then(response => {
          if (response.data) {
            console.log("Fetched group members", response.data);
            this.groupMembers = response.data;
          } else {
            console.log("No memebers found or the response is null");
            this.groupMembers = [];
          }
        })
        .catch(error => {
          console.error('Error fetching group members:', error);
        });
    },

    connectToGroupChat(groupId) {
      console.log(`Attempting to join group chat with ID: ${groupId}`);
      const joinGroupPayload = {
        type: "joinGroupChat",
        payload: {
          groupId
        }
      };
      this.showGroupChat = true;
      this.ws.send(JSON.stringify(joinGroupPayload));
    },

    handleJoinGroupChatResponse(data) {
      console.log("Handling data: ", data);
      // Update the state to indicate the user has joined the group chat
      this.currentRoomId = data.roomId;
      // You can also fetch existing messages here if needed
      this.fetchMessages(this.currentRoomId);
    },

    submitEvent() {
      // Construct the event object
      const event = {
        title: this.eventName,
        description: this.eventDescription,
        dateTime: this.eventDateTime + ":00Z",
        invitedMembers: this.selectedUserIds,
        eventCreator: this.currentUserId,
        groupId: parseInt(this.groupId, 10),
      };

      // Call the API to create the event, passing the event object
      this.createEvent(event);
    },

    createEvent(event) {
      // Placeholder for API call to create the event
      console.log('Creating event:', event);
      this.$axios.post('/api/createEvent', event)
      .then(response => {
        console.log('Created event:', response.data);

        this.eventName = '';
        this.eventDescription = '';
        this.eventDateTime = '';
        this.selectedUserIds = [];
        this.isCreatEventVisible = false;
      })
      .catch(error => {
        console.error('Error creating event:', error);
      });
      
    },

    fetchEvents() {
      const groupID = this.groupId;
      console.log(`Fetching events for GroupID: ${groupID}`);
      this.$axios.get(`api/events?groupID=${groupID}`) // Adjust URL as needed
      .then(response => {
        if (response.data) {
          console.log("These are the fetched events:", response.data);
          this.events = response.data;
        } else {
          // Handle the case where there are no events or response is null
          console.log("No events found or the response is null");
          this.events = []; // Reset to an empty array or handle accordingly
        }
      })
      .catch(error => {
        console.error("There was an error fetching the events:", error);
      });
    },

    toggleEvent(eventId) {
      this.expandedEventId = this.expandedEventId === eventId ? null : eventId;
    },

    toggleNotificationCenter() {
      this.isNotificationCenterVisible = !this.isNotificationCenterVisible;
      // If opening the notification center, check for new notifications
      if (this.isNotificationCenterVisible) {
        // List of notification types to check
        const notificationTypes = ["eventInvite", "groupInvite", "followRequestCheck", "groupJoinRequestCheck"];
        notificationTypes.forEach(type => {
          const payload = {
            type: type,
            payload: {
              userId: this.currentUserId
            }
          };
          console.log("Notifications payload for " + type + ":", payload);
          this.ws.send(JSON.stringify(payload));
        });
      }
    },

    handleResponse(notification, userResponse, responseType) {
      let responsePayload;

      if (responseType === "event") {
        responsePayload = {
          type: "eInviteResponse",
          payload: {
            responseId: notification.responseId,
            userId: this.currentUserId,
            response: userResponse,
          },
        };
      } else if (responseType === "group") {
        responsePayload = {
          type: "gInviteResponse",
          payload: {
            groupId: notification.groupId,
            userId: this.currentUserId,
            accept: userResponse,
          },
        };
      } else if (responseType === "follow") {
        responsePayload = {
          type: userResponse ? "acceptFollowRequest" : "declineFollowRequest",
          payload: {
            userId: this.currentUserId,
            followerUserId: notification.followerUserId,
          },
        };

        // Send the WebSocket message
        this.ws.send(JSON.stringify(responsePayload));

        // This includes removing the pending follow request notification
        this.notifications = this.notifications.filter(item => item.type !== 'followRequest' || item.followerUserId !== notification.followerUserId);

      } else if (responseType === "groupJoinRequest") {
        responsePayload = {
          type: userResponse ? "acceptGroupJoinRequest" : "declineGroupJoinRequest",
          payload: {
            userId: this.currentUserId,
            requestId: notification.requestId,
          },
        };
      }


      console.log(`${responseType} response payload going to backend`, responsePayload);
      this.ws.send(JSON.stringify(responsePayload));

      if (responseType === "groupJoinRequest") {
        this.notifications = this.notifications.filter(item => item.requestId !== notification.requestId);
      } else if (responseType === "event" || responseType === "group") {
        this.notifications = this.notifications.filter(item => responseType === "event" ? item.responseId !== notification.responseId : item.groupId !== notification.groupId);
      }
    },



  }
}

</script>

