// src/store/index.js
import { createStore } from 'vuex';
import router from '../router';

// Check if there's a login status stored in localStorage
const savedLoggedInStatus = localStorage.getItem('isLoggedIn');

export default createStore({
  state: {
    isLoggedIn: savedLoggedInStatus ? JSON.parse(savedLoggedInStatus) : false,
  },
  mutations: {
    setLoggedIn(state, status) {
      state.isLoggedIn = status;

      // Store the login status in localStorage
      localStorage.setItem('isLoggedIn', JSON.stringify(status));
    }
  },
  actions: {
    logIn({ commit }) {
      commit('setLoggedIn', true);
      router.push('/'); // use router.push instead of this.$router.push
    },
    logOut({ commit }) {
      commit('setLoggedIn', false);
      router.push('/welcome');
    }
  }
});
