<template>
  <div class="modal" v-if="showModal">
    <div class="form-container">
      <div class="tabs">
      <div class="tab" :class="{active: showForm === 'signup'}" @click="showForm = 'signup'">Register</div>
      <div class="tab" :class="{active: showForm === 'signin'}" @click="showForm = 'signin'">Login</div>
    </div>

    <!-- Sign In Form -->
    <form v-if="showForm === 'signin'" @submit.prevent="handleSignIn" class="form-content">
      <h2>Sign In</h2>
      <input type="text" placeholder="Email or Nickname" v-model="signInUser.credential" name="credential" required>
      <input type="password" placeholder="Password" v-model="signInUser.password" name="password" required>
      <button type="submit">Sign In</button>
      <br>
      <div v-if="signInError" class="error-message">{{ signInError }}</div>
    </form>

    <!-- Sign Up Form -->
    <form v-if="showForm === 'signup'" @submit.prevent="handleSignUp" class="form-content">
      <h2>Register</h2>
      <!-- Step 1: Required fields -->
      <div v-if="signUpStep === 1">
        <input type="email" placeholder="Email" v-model="signUpUser.email" required>
        <input type="password" placeholder="Password" v-model="signUpUser.password" required>
        <input type="text" placeholder="First Name" v-model="signUpUser.firstName" required>
        <input type="text" placeholder="Last Name" v-model="signUpUser.lastName" required>
        <input type="date" placeholder="Date of Birth" v-model="signUpUser.dateOfBirth" required>
        <select v-model="signUpUser.profilePrivacy">
          <option value="Public">Account Privacy - Public</option>
          <option value="Private">Account Privacy - Private</option>
        </select>
        <button type="button" @click="nextStep">Next</button>
        <br>
        <div v-if="signUpError" class="error-message">{{ signUpError }}</div>
      </div>

      <!-- Step 2: Optional fields -->
      <div v-if="signUpStep === 2">
        <h4>Optional</h4>
        <input type="file" @change="handleFileUpload">
        <input type="text" placeholder="Nickname" v-model="signUpUser.nickname">
        <select v-model="signUpUser.gender">
          <option value="" disabled selected hidden>Gender</option>
          <option value="">Prefer not to say</option>
          <option value="Male">Male</option>
          <option value="Female">Female</option>
        </select>
        <textarea placeholder="About Me" v-model="signUpUser.aboutMe"></textarea>
        <button type="button" @click="previousStep">Previous</button>
        <button type="submit">Sign Up</button>
        <br>
        <div v-if="signUpError" class="error-message">{{ signUpError }}</div>
      </div>
    </form>
    </div>
  </div>
</template>


<script>
import { mapState, mapActions } from 'vuex';
export default {
  data() {
    return {
      showModal: true,
      showForm: 'signin', // This can be 'signin' or 'signup'
      signInUser: {
        credential: '', // This replaces the email field to be more generic
        password: '',
      },
      signUpStep: 1,
      signUpUser: {
        email: '',
        password: '',
        firstName: '',
        lastName: '',
        dateOfBirth: null,
        avatar: null,
        nickname: '',
        aboutMe: '',
        profilePrivacy: 'Public',
        gender: ''
      },
      signInError: '',
      signUpError: '',
    };
  },
  computed: {
    ...mapState(['isLoggedIn']),
  },
  methods: {
    ...mapActions(['logIn', 'logOut']),
    nextStep() {
      this.signUpStep++;
    },
    previousStep() {
      this.signUpStep--;
    },
    handleFileUpload(event) {
      const file = event.target.files[0];
      this.signUpUser.avatar = file;
    },
    handleSignIn() {
      this.$axios.post('/api/login', {
        credential: this.signInUser.credential,
        password: this.signInUser.password
      }).then(response => {
        this.logIn(); // Vuex action to update state
        this.showModal = false;
        const { user } = response.data;
        localStorage.setItem('user', JSON.stringify(user));
        this.signInError = '';
        console.log('Sign In Success:', response);
        // Handle login success
      }).catch(error => {
        console.error('Sign In Error:', error);
        this.signInError = error.response.data.error || 'Sign In failed. Please try again.';
        // Handle login error
      });
    },

    handleSignUp() {
      const formData = new FormData();
      formData.append('Email', this.signUpUser.email);
      formData.append('Password', this.signUpUser.password);
      formData.append('FirstName', this.signUpUser.firstName);
      formData.append('LastName', this.signUpUser.lastName);
      formData.append('DateOfBirth', this.signUpUser.dateOfBirth);
      formData.append('ProfilePrivacy', this.signUpUser.profilePrivacy);
      formData.append('Gender', this.signUpUser.gender);
      if (this.signUpUser.avatar) {
        formData.append('profilePicture', this.signUpUser.avatar);
      }
      formData.append('Nickname', this.signUpUser.nickname);
      formData.append('AboutMe', this.signUpUser.aboutMe);

      this.$axios.post('/api/register', formData).then(response => {
        console.log(response);
        this.showForm = 'signin'; // Switch to Sign In form
        this.signUpError = '';
        // Fill in the email and password fields in the sign-in form
        this.signInUser.credential = this.signUpUser.email;
        this.signInUser.password = this.signUpUser.password;
      }).catch(error => {
        console.error(error);
        this.signUpError = error.response.data.error || 'Registration failed. Please try again.';
      });
    }
  }
}
</script>

